@import "../../../assets/stylesheets/consumables.scss";

// Global Overrides
.brands .content {
  align-items: flex-start;
  padding: 60px 0;
  pointer-events: none;
}

// Section Styles
.brands {
  height: 800px;
}

.brands__ouija {
  @include fit();

  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
}

.brand-quilt {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: -70px;
  margin-right: -70px;
  transform: rotate(2deg);
}

.brand-quilt > svg {
  position: absolute;
  display: inline-block;
  width: 128px;
  cursor: pointer;
  opacity: 0;
  transform-origin: center;
}

.brand-quilt > svg path {
  fill: #000000;
  opacity: 0.9;
}

.brand-quilt > svg:hover path {
  fill: #ffffff;
  opacity: 1;
}
